import { bfsSsoHttp } from '@/utils/http';

export function login(data) {
  return bfsSsoHttp.request({
    url: '/login_by_oa',
    method: 'post',
    data,
  });
}

export function logout() {
  return bfsSsoHttp.request({
    url: '/vue-admin-template/user/logout',
    method: 'post',
  });
}
